      this.dialogVisibledrug = false
      this.dialogVisiblesale = false
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          let da = {
            tag: 'member',
            op: '',
            condition: {
              id: this.addTableData.id
            },
            data: {
              type: '1',
              openid: '',
              name: this.addTableData.name,
              phone: this.addTableData.phone,
              avator: this.addTableData.avator,
              level: '1',
              sex: this.addTableData.sex,
              level_imgurl: this.addTableData.level_imgurl,
              address: this.addTableData.address,
              status: this.addTableData.status,
              label: ''
            }
          }
          if (_this.addTableData.aid === '-1') {
            da.op = 'add'
          } else {
            da.op = 'update'
          }
          this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(da), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }).then(res => {
            let da = res.data
            if (da.err > 0) {
              this.getList()
              this.dialogVisible = false
              this.sureLoading = false
              _this.$Notice.success({title: da.message})
            } else {
              this.sureLoading = false
              _this.$Notice.warning({title: da.message})
            }
          }).catch(function () {
            _this.$Notice.error({title: ''})
          })
        } else {
          return false
        }
      })
