      this.dialogVisiblecase = true
    },
    editor (index, id, list, formName) {
      this.dialogVisible = true
      this.sureLoading = false
      this.addTableData = Object.assign({}, list[index])
      this.$refs[formName].resetFields()
    },
    editordrug (index, id, list, formName) {
      this.dialogVisibledrug = true
      this.sureLoading = false
      this.addTableDatadrug = Object.assign({}, list[index])
      this.$refs[formName].resetFields()
    },
    editorsale (index, id, list, formName) {
      this.dialogVisiblesale = true
      this.sureLoading = false
      this.addTableDatasale = Object.assign({}, list[index])
      this.saleList()
      this.$refs[formName].resetFields()
    },
    editoragent (index, id, list, formName) {
      this.dialogVisibleagent = true
      this.sureLoading = false
      this.addTableDataagent = Object.assign({}, list[index])
      this.saleList()
      this.$refs[formName].resetFields()
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          'type': this.formInline.type
        }
      }
      if (this.formInline.type === 1) {
        this.patient = true
      } else {
        this.patient = false
      }
      if (this.formInline.type === 2) {
        this.drugshop = true
      } else {
        this.drugshop = false
      }
      if (this.formInline.type === 3 || this.formInline.type === 4) {
        this.saleagent = true
      } else {
        this.saleagent = false
      }
      this.$axios.post(domain.testUrl + '/admin/member/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          this.loading = false
          this.tableData = []
          this.total = 0
          this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: ''})
      })
    },
    addcase () {
      let _this = this
      this.shopname = ''
      var pa = this.currentPage
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          'type': '2'
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDataShopname = da.data.list
        } else {
          this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: ''})
      })
    },
    saleList (isSearch) {
      let d = {
        tag: 'product',
        op: 'getlist',
        condition: {
          'name': this.formInline.name,
          'sn': this.formInline.sn,
          'enddate': this.formInline.enddate,
          'stock': this.formInline.stock
        }
      }
      this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatagood = da.data.list
          this.loading = false
        } else {
          this.$Notice.warning({title: da.message})
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: ''})
      })
    },
    // 
    detail (id) {
      if (this.formInline.type === 1) {
        this.patientdetial = true
        this.allname = false
        this.sg = false
      } else {
        this.patientdetial = false
      }
      if (this.formInline.type === 2) {
        this.drug = true
        this.allname = true
        this.sg = false
      } else {
        this.drug = false
      }
      if (this.formInline.type === 3) {
        this.saledetail = true
        this.allname = true
        this.sg = true
      } else {
        this.saledetail = false
      }
      if (this.formInline.type === 4) {
        this.agentdetail = true
        this.allname = true
        this.sg = true
      } else {
        this.agentdetail = false
      }
      let d = {
        tag: 'member',
        op: 'getone',
        condition: {
          'id': id
        }
      }
      this.dialogdetail = true
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatadetail = da.data
        } else {
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: ''})
      })
    },
    carse (id) {

      // let d = {
      //   tag: 'member',
      //   op: 'case_history',
      //   condition: {
      //     'id': id
      //   }
      // }
      // this.dialogcase = true
      // this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
      //   headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      // }).then(res => {
      //   let da = res.data
      //   if (da.err > 0) {
      //     this.tableDatacase = da.data.goodsinfo.list
      //     this.totaldetail = da.data.goodsinfo.total
      //   } else {
      //     this.loading = false
      //   }
      // }).catch(function () {
      //   this.$Notice.error({title: ''})
      // })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 0
      } else {
        status = 1
      }
      let d = {
        tag: 'member',
        op: 'state',
        condition: {
          id: id
        },
        data: {
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$Notice.success({title: da.message})
        } else {
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: ''})
      })
    }
  }
}
</script>



